import React from 'react'
import {Container, Row, Col, Form, Button } from 'react-bootstrap';

export default function HeaderForm() {
 return (
  <div>
 <Container>
  <Row>
    <Col sm={7}></Col>
    <Col sm={4}>
    <Form>
     <Form.Group controlId="formGroupEmail">
       <Form.Control type="email" placeholder="Email" className='input form_elements'/>
     </Form.Group>
     <Form.Group controlId="formGroupPassword">
       <Form.Control type="password" placeholder="Password" className='input form_elements'/>
     </Form.Group>
     <Button className='start form_elements' type="submit">
      РОЗПОЧАТИ!
   </Button>
  </Form>
    </Col>
  </Row>
</Container>
  </div>
 )
}

